import { CONFIG } from '../../constants/config'
import { BaseModal } from './BaseModal'
import { useInterfaceLanguage } from '../../hooks/useInterfaceLanguage'

type Props = {
  isOpen: boolean
  handleClose: () => void
}

export const AboutModal = ({ isOpen, handleClose }: Props) => {
  const { texts } = useInterfaceLanguage()
  const aboutTexts = texts?.about || {
    title: "About This Game",
    description: "This is an open source adaptation of the word guessing game Wordle, designed for Polynesian languages.",
    adaptedBy: "Adapted by",
    originalCode: "the original code",
    originalCodeBy: "by",
    customizeText: "and customize it for another language!",
    wordsSourced: "The words for this game were sourced from",
    playOriginal: "Or, you can also",
    playOriginalLink: "play the original here",
    links: {
      originalWordle: "https://www.powerlanguage.co.uk/wordle/",
      originalCode: "https://github.com/hannahcode/wordle",
      anyLanguageFork: "https://github.com/roedoejet/AnyLanguage-Wordle",
      hannahPark: "https://www.hannahmariepark.com/"
    },
    credits: {
      originalAuthor: "Hannah Park",
      forkAuthor: "Aidan Pine"
    }
  }

  return (
    <BaseModal title={aboutTexts.title} isOpen={isOpen} handleClose={handleClose}>
      <p className="text-sm text-gray-500">
        {aboutTexts.description} {aboutTexts.adaptedBy}{' '}
        <a href={CONFIG.authorWebsite} className="underline font-bold">
          {CONFIG.author}
        </a>{' '}
        - check out{' '}
        <a
          href={aboutTexts.links.originalCode}
          className="underline font-bold"
        >
          {aboutTexts.originalCode}
        </a>{' '}
        {aboutTexts.originalCodeBy}{' '}
        <a
          href={aboutTexts.links.hannahPark}
          className="underline font-bold"
        >
          {aboutTexts.credits.originalAuthor}
        </a>{' '}
        or have a look at{' '}
        <a
          href={aboutTexts.links.anyLanguageFork}
          className="underline font-bold"
        >
          {aboutTexts.credits.forkAuthor}'s fork
        </a>{' '}
        {aboutTexts.customizeText} {aboutTexts.wordsSourced}{' '}
        <a href={CONFIG.wordListSourceLink} className="underline font-bold">
          {CONFIG.wordListSource}
        </a>
        . {aboutTexts.playOriginal}{' '}
        <a
          href={aboutTexts.links.originalWordle}
          className="underline font-bold"
        >
          {aboutTexts.playOriginalLink}
        </a>
      </p>
    </BaseModal>
  )
}
