// Centralized game configuration
// Replaces hardcoded values throughout the app

import { getAvailableLanguages, getLanguageCodeMap, getDefaultLanguage } from '../languages/registry'

export const GAME_CONFIG = {
  // Language configuration
  availableLanguages: getAvailableLanguages(),
  languageCodeMap: getLanguageCodeMap(),
  defaultLanguage: getDefaultLanguage(),
  
  // Word length configuration
  supportedWordLengths: [5, 6], // Can be expanded dynamically in future
  
  // Game mechanics
  alertTimeMs: 2000,
  longPressDelayMs: 400,
  popupHideDelayMs: 200,
  
  // Storage keys
  storageKeys: {
    languageSettings: 'kimiKupuLanguageSettings',
    gameState: 'kimiKupuGameState',
    stats: 'kimiKupuStats',
  },
  
  // Build info
  buildVersion: process.env.REACT_APP_BUILD_VERSION || Date.now().toString(),
  
  // Language-specific features
  languageSpecificMessages: {
    hawaiian: {
      winMessages: ['Hoʻomaikaʻi!', 'Hana pono!', 'Maikaʻi loa!'],
      gameLost: 'ʻO ka hua ʻōlelo',
    },
    maori: {
      winMessages: ['Ka pai!', 'Tino pai!', 'Ataahua!'],
      gameLost: 'Ko te kupu',
    },
    tahitian: {
      winMessages: ['Māuruuru!', 'Aita pe!', 'Noa noa!'],
      gameLost: 'Te parau',
    },
    samoan: {
      winMessages: ['Lelei!', 'Taumafai!', 'Silisili!'],
      gameLost: 'O le upu',
    },
  },
} as const

// Dynamic word length helpers
export const getWordLengthOptions = () => {
  return GAME_CONFIG.supportedWordLengths.map(length => ({
    value: length,
    label: `${length} Letters`
  }))
}

// Attempts formula - can be made language-specific later
export const calculateMaxAttempts = (wordLength: number): number => {
  return wordLength + 1 // 5-letter = 6 attempts, 6-letter = 7 attempts
}

// Language-specific message helpers
export const getWinMessage = (gameLanguage: string, attemptNumber?: number): string => {
  const messages = GAME_CONFIG.languageSpecificMessages[gameLanguage as keyof typeof GAME_CONFIG.languageSpecificMessages]?.winMessages
  if (!messages) {
    return 'Congratulations!' // Fallback
  }
  
  // Return different message based on attempt number, or random if not specified
  if (attemptNumber !== undefined && attemptNumber < messages.length) {
    return messages[attemptNumber]
  }
  
  return messages[Math.floor(Math.random() * messages.length)]
}

export const getLossMessage = (gameLanguage: string): string => {
  const message = GAME_CONFIG.languageSpecificMessages[gameLanguage as keyof typeof GAME_CONFIG.languageSpecificMessages]?.gameLost
  return message || 'The word was' // Fallback
}